body {
  margin: 0;
  font-family: "Work Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #fffc00;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffffff, #fffc00);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Pokecard {
  /* border: 2px solid mediumseagreen; */
  /* width: 300px; */
  width: 140px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: rgb(207, 223, 187);
  border-radius: 3px;
  line-height: 1.4;
}

.Pokecard-data {
  font-size: 80%;
  text-transform: capitalize;
  font-family: 'Handlee', cursive;
  letter-spacing: .5px;
}

.Pokecard-title {
  margin-bottom: .75rem;
  font-weight: bold;
  font-size: 93%;
  font-family: 'Philosopher', sans-serif;
  letter-spacing: .5px;
  text-transform: capitalize;
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
}

.Pokecard img:hover {
  transform: scale(1.1);
  opacity: 0.9;
  transition: all ease 500ms;
}
.DiscardCard {
  width: 165px;
  max-height: 223px;
  padding: 10px 20px;
  margin: 1rem;
  color: #fff;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  /* background: rgba(156, 168, 146, 0.8); */
  background: rgb(107, 119, 107);
  border-radius: 3px;
  border: 1.5px ridge rgba(0, 0, 0, 0.2);

}

.DiscardCard-title {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-family: "Amatic SC", cursive;
  font-weight: bold;
  letter-spacing: 1px;
}

.DiscardCard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 110px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 1rem;
  margin-top: .3rem;
  border: 1.5px ridge #6464646c;
  box-shadow: -17px 5px 13px -5px #616d60;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);

}

.DiscardCard-data {
  font-size: 85%;
  text-transform: capitalize;
  font-family: 'Handlee', cursive;
  letter-spacing: .5px;
}
.CompletedSet-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

