.CompletedSet-content {
  height: 50px;
  width: 75px;
  margin: .2rem;
  font-family: 'Annie Use Your Telescope', cursive;
  font-weight: bold;
  color: white;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  text-align: center;
  background-color: #abb89e;
  padding-top: .85rem;
  border-radius: 3px;
  border: 1px ridge rgba(0, 0, 0, 0.2);
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
}
.CardDeck {
  width: 165px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background: #b00020d2;
  border-radius: 3px;
  border: 1px ridge rgba(0, 0, 0, 0.2);

}

.CardDeck-title {
  margin-bottom: 1rem;
  color: white;
  font-family: "Amatic SC", cursive;
  font-weight: bold;
  letter-spacing: 1px;
}

.CardDeck-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 145px;
  background-color: rgb(86, 94, 93);
  border-radius: 5px;
  margin-bottom: 1rem;
  border: 2px ridge #616d606c;
}

.CardDeck-image img {
  box-shadow: -17px 25px 13px -5px#616d60;
  border-radius: 50%;
}
