.Pokedex {
  text-align: center;
  margin: 0;
}

.Pokedex-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.scorelist-wrapper {
  margin: 30px 0;
}

.game-wrapper {
  height: 100%;
  width: 100%;
}

.CompleteSet-1 {
  margin-top: 30px;
  margin-bottom: 150px;
}

.CompleteSet-1 {
  margin-top: 80px;
  margin-bottom: 30px;
}

.border-me {
  border-left: 1px rgba(169, 169, 169, 0.4) solid;
}
header {
  margin: -20px;
  text-align: center;
}

header h1 {
  font-size: 88px;
  font-weight: bold;
  font-family: "Orbitron", sans-serif;
  color: rgb(15, 37, 109);
  text-shadow: -2px -1px 0 rgb(255, 0, 0), 2px -1px 0 rgb(255, 196, 0),
    -2px 1px 0 rgb(0, 0, 0), 2px 1px 0 #000;
}

header h1:hover {
  transform: scale(1.1);
  opacity: 0.9;
  transition: all ease 500ms;
}

header .image {
  border-radius: 50%;
  box-shadow: -15px 25px 12px -5px rgb(0, 0, 0);
}

header h3 {
  font-size: 2em;
  font-weight: 700;
  letter-spacing: 0.5px;
  font-family: "Amatic SC", cursive;
}

.battleCard {
  background: #000428;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #004e92, #000428);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin: 80px auto;
  height: 450px;
  max-width: 600px;
  letter-spacing: 1px;
  box-shadow: 10px -20px 30px 20px rgba(7, 7, 7, 0.8);
  /* box-shadow: -17px 10px 12px -5px rgba(0, 0, 0, 0.56); */
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 15px;
  border: 5px ridge #004e92;
}

.battleCard h5 {
  margin-top: 45px;
  margin-bottom: 25px;
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 26px;
  font-weight: bold;
  font-family: "Piedra", cursive;
  letter-spacing: 2px;
  color: rgb(255, 166, 0);
  border-bottom: 5px ridge rgb(255, 166, 0);
  border-radius: 4px;
  box-shadow: -17px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-shadow: -1px -1px 0 rgb(0, 0, 0), 1px -1px 0 rgb(0, 0, 0),
    -1px 1px 0 rgb(0, 0, 0), 1px 1px 0 #000;
  /* text-shadow: -1px -1px 0 rgb(231, 247, 7), 1px -1px 0 rgb(243, 4, 4), -1px 1px 0 rgb(0, 0, 0), 1px 1px 0 #000; */
}

Button {
  box-shadow: -17px 10px 12px -7px rgba(63, 27, 27, 0.56);
  border-radius: 10px !important;
  letter-spacing: 0.8px !important;
  font-family: "Special Elite", cursive !important;
}

Button:hover {
  transform: scale(1.1);
  color: white !important;
  opacity: 0.9;
  transition: all ease 500ms;
  border: 1px solid white !important;
}

.game-rules {
  color: white !important;
  border: 1px solid white !important;
}

@media (max-width: 768px) {
  header h1 {
    font-size: 44px;
    margin: 10px;
    padding-top: 50px;
  }

  .image {
    width: 40px;
    height: 40px;
  }

  header h3 {
    font-size: 24px;
  }

  .battleCard {
    height: 360px;
    width: 260px;
    box-shadow: 22px 20px 30px -2px rgba(0, 0, 0, 0.56);
    opacity: .97;
    border: 10px groove #004e92;
    border-radius: 60px;
  }

  .battleCard h5 {
    font-size: 18px;
    font-weight: 500;
    border-radius: 20px;
    margin-top: 27px;
    margin-bottom: 12px;
    opacity: .95;
  }

  Button {
    font-size: 12px !important;
    margin-top: .75rem !important;
    border-radius: 5px !important;
  }
}
